export const getDelay = (params, opts) => {
    let obj = {
            'errcode': 0,
            'errmsg': 'ok',
            'data': Date.now()
        },
        delay = params.delay || 200;
    return Promise.resolve(() => setTimeout(() => { return Promise.resolve(obj); }, delay));
};
